import React from 'react'

import {
    Container,
    Row,
    Col,
    Image
} from 'react-bootstrap';
import './Profile.scss';

import Social from './Social';
import profilePic from '../../images/avatar.png';
import profilePicNeon from '../../images/avatar-neon.png';

const data = [
    {
        title: "Teléfono: ",
        info: "+52 1 33 2495 4452",
    },
    {
        title: "Email: ",
        info: "krivozznova@gmail.com"
    },
  
]

const Profile = ({neon}) => {
    return (
        <div className={neon ? 'profile__neon' : 'profile'}>
            <div className="wallpaper" />
            <div className="dark" />

            <Container className="box">
                <Row className="info">
                    <Col xs={12} md={4}>
                        <Image src={ neon ? profilePicNeon : profilePic} fluid  roundedCircle />
                    </Col>

                    <Col xs={12} md={8} className="info__data">
                        <span>Hola</span>
                        <p>Felipe Cruz</p>
                        <p>Fullstack developer</p>

                        <hr />

                        {data.map( (item, key) => {
                            return(
                                <div key={key} className="more-info">
                                    <div className="item">
                                            <p>{item.title}</p>
                                            <p>{item.info}</p>
                                    </div>
                                </div>
                            )
                        })}
                    </Col>
                </Row>
                <Social neon={neon}/>
            </Container>
        </div>
    )
}

export default Profile
